.header-bg{
    width: 100%;
    position: fixed;
    top: 0;
    background-color: #f6f1f1;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px;
    gap: 50px;
    font-family: Arial, Helvetica, sans-serif;
    z-index: 1;
}

.linktag{
    width: 100px;
    padding: 10px;
    background-color: transparent;
    border: 1px solid #959595;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #959595;
    border-radius: 10px;
}
.active{
    width: 100px;
    padding: 10px;
    background-color: rgb(37, 37, 37);
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #ffffff;
    border-radius: 10px;
    box-shadow: inset 0px 0px 40px 5px #ff4444;
}