:root {
  --bg-color: #cd6363;
  --sec-color: #f17171;
}
.sw-mainbody {
  width: 100%;
  height: 100vh;
  background-color: var(--bg-color);
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
}
.stopwatch {
  text-align: center;
  margin: 120px auto;
  width: 800px;
  background-color: var(--sec-color);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.timer-container{
    font-size: 5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #ffffff;
    display: flex;
    flex-direction: row;
}
.timer-container span{
    text-align: left;
    width: 100px;
    padding: 10px;
}

button{
    font-family: Arial, Helvetica, sans-serif;
    margin: 20px;
    padding: 10px 20px;
    font-size: 14px;
    font-weight: 400;
    cursor: pointer;
    border: none;
    background-color: transparent;
    color: #ffffff;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.startbtn, .resumebtn, .stopbtn, .resetbtn{
    font-weight: 600;
    width: 220px;
    height: 70px;
    font-size: 24px;
    background-color: #ffffff;
    color: var(--bg-color);
}
.resetbtn{
    background-color: #ffc6a5;

}