:root{
  --main-color: #cd6363;
  --second-color: #f17171;
}
.mainbody{
  width: 100%;
  height: 100vh;
  background-color: var(--main-color);
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
}
.pomodoro {
    text-align: center;
    margin: 120px auto;
    width: 500px;
    background-color: var(--second-color);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .timer {
    font-size: 10rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #ffffff;
  }
  
  .contorls {
    height: 50px;
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-direction: row;
  }
  
  button {
    font-family: Arial, Helvetica, sans-serif;
    width: 120px;
    padding: 10px 20px;
    font-size: 14px;
    font-weight: 400;
    cursor: pointer;
    border: none;
    background-color: transparent;
    color: #ffffff;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  
  .sessions {
    margin-top: 20px;

  }
  
  .sessions label {
    font-size: 16px;
    margin-left: 20px;
    margin-right: 10px;
    color: #151515;
  }
  
  .sessions input {
    width: 50px;
    padding: 5px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
  }
  


  .startButton, .pauseButton{
    font-weight: 600;
    width: 220px;
    height: 70px;
    font-size: 24px;
    background-color: #ffffff;
    color: var(--main-color);
  }
  .pauseButton{
    box-shadow: 2px 2px 0px 2px #00000043;
    translate: 0px -2px;
  }
  .timeButtons{
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

  .activeButton{
    background-color: var(--main-color);
  }